import React from 'react'
import { cva, type VariantProps } from 'class-variance-authority'
import { cn } from '@/utils/cn'

const badgeVariants = cva(
  'inline-flex items-center rounded-full px-2.5 py-0.5 text-xs font-medium transition-colors',
  {
    variants: {
      variant: {
        default: 'bg-gray-100 text-gray-800',
        primary: 'bg-primary-100 text-primary-800',
        secondary: 'bg-gray-100 text-gray-600',
        success: 'bg-green-100 text-green-800',
        warning: 'bg-yellow-100 text-yellow-800',
        danger: 'bg-red-100 text-red-800',
        info: 'bg-blue-100 text-blue-800',
        //    (MVP )
        available: 'bg-green-100 text-green-800', // 
        reserved: 'bg-yellow-100 text-yellow-800', // 
        sold: 'bg-gray-100 text-gray-600', // 
        //   (MVP )
        sale: 'bg-red-100 text-red-700', // 
        jeonse: 'bg-green-100 text-green-700', // 
        monthly: 'bg-blue-100 text-blue-700', // 
        //  
        tenant: 'bg-tenant-primary bg-opacity-10 text-tenant-primary',
      },
      size: {
        sm: 'px-2 py-0.5 text-xs',
        md: 'px-2.5 py-0.5 text-xs',
        lg: 'px-3 py-1 text-sm',
      },
    },
    defaultVariants: {
      variant: 'default',
      size: 'md',
    },
  }
)

export interface BadgeProps
  extends React.HTMLAttributes<HTMLDivElement>,
    VariantProps<typeof badgeVariants> {
  children: React.ReactNode
}

const Badge = React.forwardRef<HTMLDivElement, BadgeProps>(
  ({ className, variant, size, children, ...props }, ref) => {
    return (
      <div
        ref={ref}
        className={cn(badgeVariants({ variant, size }), className)}
        {...props}
      >
        {children}
      </div>
    )
  }
)

Badge.displayName = 'Badge'

//    (MVP )
export const PropertyStatusBadge: React.FC<{
  status: '' | '' | '' | '' | ''
  className?: string
}> = ({ status, className }) => {
  const variantMap = {
    '': 'available',
    '': 'reserved', 
    '': 'sold',
    '': 'secondary',
    '': 'danger',
  } as const

  return (
    <Badge variant={variantMap[status]} className={className}>
      {status}
    </Badge>
  )
}

//   (MVP )
export const TransactionTypeBadge: React.FC<{
  type: '' | '' | '' | ''
  className?: string
}> = ({ type, className }) => {
  const variantMap = {
    '': 'sale',
    '': 'jeonse',
    '': 'monthly',
    '': 'info',
  } as const

  return (
    <Badge variant={variantMap[type]} className={className}>
      {type}
    </Badge>
  )
}

export { Badge, badgeVariants }