// ============================================================================
// RBAC (Role-Based Access Control)  
// ============================================================================

import type { UserRole } from './user'

export interface RoleDefinition {
  role: UserRole
  name: string
  description: string
  permissions: string[]
  hierarchy: number // 0  
}

