//   
export interface UserInvitation {
  id: string
  tenant_id: string
  invited_by: string
  email: string
  role: UserRole
  status: 'pending' | 'accepted' | 'expired' | 'cancelled'
  invited_at: string
  expires_at: string
  accepted_at?: string
}

//    

//    
export type PermissionCheck = {
  resource: string
  action: string
}

export type PermissionResult = {
  allowed: boolean
  reason?: string
}