import React, { useState } from 'react'
import { X, Save } from 'lucide-react'
import { Button, Input, Select, Card, CardHeader, CardTitle, CardContent, Modal } from '@/components/ui'
import type { CreatePropertyData, PropertyType, TransactionType, PropertyStatus } from '@/types'

interface PropertyCreateFormProps {
  isOpen: boolean
  onClose: () => void
  onSubmit: (data: CreatePropertyData) => Promise<void>
  loading?: boolean
}

const PROPERTY_TYPES: PropertyType[] = [
  '', '', '', '', '', '', '', ''
]

const TRANSACTION_TYPES: TransactionType[] = [
  '', '', '', ''
]

const PROPERTY_STATUS: PropertyStatus[] = [
  '', '', '', '', ''
]

export const PropertyCreateForm: React.FC<PropertyCreateFormProps> = ({
  isOpen,
  onClose,
  onSubmit,
  loading = false
}) => {
  const [formData, setFormData] = useState<CreatePropertyData>({
    title: '',
    type: '',
    transaction_type: '',
    address: '',
    area: 0,
    floor: 1,
    total_floors: 1,
    rooms: 1,
    bathrooms: 1,
    parking: false,
    elevator: false,
    status: ''
  })

  const [errors, setErrors] = useState<Partial<Record<keyof CreatePropertyData, string>>>({})

  const handleInputChange = (field: keyof CreatePropertyData, value: any) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }))
    
    //  
    if (errors[field]) {
      setErrors(prev => ({
        ...prev,
        [field]: undefined
      }))
    }
  }

  const validateForm = (): boolean => {
    const newErrors: Partial<Record<keyof CreatePropertyData, string>> = {}

    if (!formData.title.trim()) {
      newErrors.title = ' '
    }

    if (!formData.address.trim()) {
      newErrors.address = ' '
    }

    if (formData.area <= 0) {
      newErrors.area = ' 0  '
    }

    if (formData.floor < 1) {
      newErrors.floor = ' 1  '
    }

    if (formData.total_floors < formData.floor) {
      newErrors.total_floors = '      '
    }

    if (formData.rooms < 1) {
      newErrors.rooms = '  1  '
    }

    if (formData.bathrooms < 1) {
      newErrors.bathrooms = '  1  '
    }

    //    
    if (formData.transaction_type === '' && (!formData.price || formData.price <= 0)) {
      newErrors.price = ' '
    }

    if (formData.transaction_type === '' && (!formData.deposit || formData.deposit <= 0)) {
      newErrors.deposit = ' '
    }

    if (formData.transaction_type === '') {
      if (!formData.deposit || formData.deposit <= 0) {
        newErrors.deposit = ' '
      }
      if (!formData.monthly_rent || formData.monthly_rent <= 0) {
        newErrors.monthly_rent = ' '
      }
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!validateForm()) return

    try {
      await onSubmit(formData)
      
      //  
      setFormData({
        title: '',
        type: '',
        transaction_type: '',
        address: '',
        area: 0,
        floor: 1,
        total_floors: 1,
        rooms: 1,
        bathrooms: 1,
        parking: false,
        elevator: false,
        status: ''
      })
      setErrors({})
      onClose()
    } catch (error) {
      console.error('  :', error)
    }
  }

  if (!isOpen) return null

  return (
    <Modal isOpen={isOpen} onClose={onClose} size="lg">
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <CardTitle>  </CardTitle>
            <Button
              variant="ghost"
              size="sm"
              onClick={onClose}
              className="p-1 h-8 w-8"
            >
              <X size={16} />
            </Button>
          </div>
        </CardHeader>
        
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-6">
            {/*   */}
            <div className="space-y-4">
              <h3 className="text-lg font-medium text-gray-900"> </h3>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="md:col-span-2">
                  <Input
                    label=" "
                    value={formData.title}
                    onChange={(e) => handleInputChange('title', e.target.value)}
                    error={errors.title}
                    placeholder=":    "
                    required
                  />
                </div>
                
                <Select
                  label=" "
                  value={formData.type}
                  onChange={(e) => handleInputChange('type', e.target.value as PropertyType)}
                  options={PROPERTY_TYPES.map(type => ({ value: type, label: type }))}
                  required
                />
                
                <Select
                  label=" "
                  value={formData.transaction_type}
                  onChange={(e) => handleInputChange('transaction_type', e.target.value as TransactionType)}
                  options={TRANSACTION_TYPES.map(type => ({ value: type, label: type }))}
                  required
                />
              </div>
            </div>

            {/*   */}
            <div className="space-y-4">
              <h3 className="text-lg font-medium text-gray-900"> </h3>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="md:col-span-2">
                  <Input
                    label=""
                    value={formData.address}
                    onChange={(e) => handleInputChange('address', e.target.value)}
                    error={errors.address}
                    placeholder=":    123-45"
                    required
                  />
                </div>
                
                <Input
                  label=" "
                  value={formData.detailed_address || ''}
                  onChange={(e) => handleInputChange('detailed_address', e.target.value)}
                  placeholder=": 123 456"
                />
              </div>
            </div>

            {/*   */}
            <div className="space-y-4">
              <h3 className="text-lg font-medium text-gray-900"> </h3>
              
              <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                <Input
                  label=" (m)"
                  type="number"
                  value={formData.area || ''}
                  onChange={(e) => handleInputChange('area', parseFloat(e.target.value) || 0)}
                  error={errors.area}
                  placeholder="85.0"
                  min="0"
                  step="0.1"
                  required
                />
                
                <Input
                  label=""
                  type="number"
                  value={formData.floor || ''}
                  onChange={(e) => handleInputChange('floor', parseInt(e.target.value) || 1)}
                  error={errors.floor}
                  placeholder="15"
                  min="1"
                  required
                />
                
                <Input
                  label=" "
                  type="number"
                  value={formData.total_floors || ''}
                  onChange={(e) => handleInputChange('total_floors', parseInt(e.target.value) || 1)}
                  error={errors.total_floors}
                  placeholder="25"
                  min="1"
                  required
                />
                
                <Input
                  label=" "
                  type="number"
                  value={formData.rooms || ''}
                  onChange={(e) => handleInputChange('rooms', parseInt(e.target.value) || 1)}
                  error={errors.rooms}
                  placeholder="3"
                  min="1"
                  required
                />
                
                <Input
                  label=" "
                  type="number"
                  value={formData.bathrooms || ''}
                  onChange={(e) => handleInputChange('bathrooms', parseInt(e.target.value) || 1)}
                  error={errors.bathrooms}
                  placeholder="2"
                  min="1"
                  required
                />
              </div>
              
              <div className="flex space-x-6">
                <label className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    checked={formData.parking}
                    onChange={(e) => handleInputChange('parking', e.target.checked)}
                    className="rounded border-gray-300 text-primary-600 focus:ring-primary-500"
                  />
                  <span className="text-sm font-medium text-gray-700"> </span>
                </label>
                
                <label className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    checked={formData.elevator}
                    onChange={(e) => handleInputChange('elevator', e.target.checked)}
                    className="rounded border-gray-300 text-primary-600 focus:ring-primary-500"
                  />
                  <span className="text-sm font-medium text-gray-700"></span>
                </label>
              </div>
            </div>

            {/*   */}
            <div className="space-y-4">
              <h3 className="text-lg font-medium text-gray-900"> </h3>
              
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                {formData.transaction_type === '' && (
                  <Input
                    label=" ()"
                    type="number"
                    value={formData.price || ''}
                    onChange={(e) => handleInputChange('price', parseInt(e.target.value) || undefined)}
                    error={errors.price}
                    placeholder="35000"
                    min="0"
                    required
                  />
                )}
                
                {(formData.transaction_type === '' || formData.transaction_type === '') && (
                  <Input
                    label={formData.transaction_type === '' ? ' ()' : ' ()'}
                    type="number"
                    value={formData.deposit || ''}
                    onChange={(e) => handleInputChange('deposit', parseInt(e.target.value) || undefined)}
                    error={errors.deposit}
                    placeholder="21000"
                    min="0"
                    required
                  />
                )}
                
                {formData.transaction_type === '' && (
                  <Input
                    label=" ()"
                    type="number"
                    value={formData.monthly_rent || ''}
                    onChange={(e) => handleInputChange('monthly_rent', parseInt(e.target.value) || undefined)}
                    error={errors.monthly_rent}
                    placeholder="65"
                    min="0"
                    required
                  />
                )}
              </div>
            </div>

            {/*   */}
            <div className="space-y-4">
              <h3 className="text-lg font-medium text-gray-900"> </h3>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <Input
                  label=" "
                  value={formData.landlord_name || ''}
                  onChange={(e) => handleInputChange('landlord_name', e.target.value)}
                  placeholder=""
                />
                
                <Input
                  label=" "
                  value={formData.landlord_phone || ''}
                  onChange={(e) => handleInputChange('landlord_phone', e.target.value)}
                  placeholder="010-1234-5678"
                />
              </div>
            </div>

            {/*   */}
            <div className="space-y-4">
              <h3 className="text-lg font-medium text-gray-900"> </h3>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <Input
                  label=" "
                  type="date"
                  value={formData.exit_date || ''}
                  onChange={(e) => handleInputChange('exit_date', e.target.value)}
                />
                
                <Select
                  label=" "
                  value={formData.status || ''}
                  onChange={(e) => handleInputChange('status', e.target.value as PropertyStatus)}
                  options={PROPERTY_STATUS.map(status => ({ value: status, label: status }))}
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                   
                </label>
                <textarea
                  value={formData.description || ''}
                  onChange={(e) => handleInputChange('description', e.target.value)}
                  placeholder="    "
                  rows={4}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
                />
              </div>
            </div>

            {/*   */}
            <div className="flex justify-end space-x-3 pt-6 border-t border-gray-200">
              <Button
                type="button"
                variant="outline"
                onClick={onClose}
                disabled={loading}
              >
                
              </Button>
              <Button
                type="submit"
                variant="primary"
                loading={loading}
                leftIcon={<Save size={16} />}
              >
                 
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>
    </Modal>
  )
}