// ============================================================================
//   - Supabase Auth 
// ============================================================================

import React, { createContext, useContext, useEffect, useState } from 'react'
import { Session, AuthError } from '@supabase/supabase-js'
import { supabase } from '@/services/supabase'
import type { AuthUser, SignUpData, SignInData } from '@/types'

interface AuthContextType {
  user: AuthUser | null
  session: Session | null
  loading: boolean
  signUp: (data: SignUpData) => Promise<any>
  signIn: (data: SignInData) => Promise<any>
  signOut: () => Promise<{ error: AuthError | null }>
  resetPassword: (email: string) => Promise<{ error: AuthError | null }>
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export const useAuth = () => {
  const context = useContext(AuthContext)
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider')
  }
  return context
}

interface AuthProviderProps {
  children: React.ReactNode
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [user, setUser] = useState<AuthUser | null>(null)
  const [session, setSession] = useState<Session | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    //       ( )
    if (import.meta.env.VITE_APP_ENV === 'development' && import.meta.env.DEV) {
      const demoUser: AuthUser = {
        id: '00000000-0000-0000-0000-000000000001',
        email: 'demo@propertydesk.com',
        name: ' ',
        role: 'owner',
        tenant_id: '00000000-0000-0000-0000-000000000001',
        avatar_url: null,
        created_at: new Date().toISOString(),
        last_sign_in_at: new Date().toISOString(),
      }
      
      setUser(demoUser)
      setLoading(false)
      return
    }

    //    ( )
    const getSession = async () => {
      try {
        // 5  
        const timeoutPromise = new Promise((_, reject) => 
          setTimeout(() => reject(new Error('Session check timeout')), 5000)
        )
        
        const sessionPromise = supabase.auth.getSession()
        
        const { data: { session }, error } = await Promise.race([
          sessionPromise, 
          timeoutPromise
        ]) as any
        
        if (error) {
          console.error('Error getting session:', error)
          setLoading(false)
          return
        }
        
        setSession(session)
        if (session?.user) {
          try {
            //     ( )
            const profilePromise = supabase
              .from('user_profiles')
              .select('*')
              .eq('id', session.user.id)
              .single()
              
            const profileTimeoutPromise = new Promise((_, reject) => 
              setTimeout(() => reject(new Error('Profile fetch timeout')), 3000)
            )
            
            const { data: profile } = await Promise.race([
              profilePromise,
              profileTimeoutPromise
            ]) as any

            setUser({
              id: session.user.id,
              email: session.user.email!,
              name: profile?.name || session.user.user_metadata?.name || '',
              role: profile?.role || 'owner',
              tenant_id: profile?.tenant_id || null,
              avatar_url: profile?.avatar_url || null,
              created_at: session.user.created_at,
              last_sign_in_at: session.user.last_sign_in_at || null,
            })
          } catch (profileError) {
            console.error('Error fetching profile:', profileError)
            //        
            setUser({
              id: session.user.id,
              email: session.user.email!,
              name: session.user.user_metadata?.name || session.user.email!,
              role: 'owner',
              tenant_id: null,
              avatar_url: null,
              created_at: session.user.created_at,
              last_sign_in_at: session.user.last_sign_in_at || null,
            })
          }
        }
      } catch (error) {
        console.error('Error in getSession:', error)
      } finally {
        setLoading(false)
      }
    }

    getSession()

    //     ( )
    const { data: { subscription } } = supabase.auth.onAuthStateChange(
      async (event, session) => {
        console.log(' Auth state changed:', event, session?.user?.id, session?.user?.email)
        
        setSession(session)
        
        if (session?.user) {
          try {
            //     ( )
            const profilePromise = supabase
              .from('user_profiles')
              .select('*')
              .eq('id', session.user.id)
              .single()
              
            const profileTimeoutPromise = new Promise((_, reject) => 
              setTimeout(() => reject(new Error('Profile fetch timeout')), 3000)
            )
            
            const { data: profile } = await Promise.race([
              profilePromise,
              profileTimeoutPromise
            ]) as any

            setUser({
              id: session.user.id,
              email: session.user.email!,
              name: profile?.name || session.user.user_metadata?.name || '',
              role: profile?.role || 'owner',
              tenant_id: profile?.tenant_id || null,
              avatar_url: profile?.avatar_url || null,
              created_at: session.user.created_at,
              last_sign_in_at: session.user.last_sign_in_at || null,
            })
          } catch (profileError) {
            console.error('Error fetching profile in auth state change:', profileError)
            //        
            setUser({
              id: session.user.id,
              email: session.user.email!,
              name: session.user.user_metadata?.name || session.user.email!,
              role: 'owner',
              tenant_id: null,
              avatar_url: null,
              created_at: session.user.created_at,
              last_sign_in_at: session.user.last_sign_in_at || null,
            })
          }
        } else {
          setUser(null)
        }
        
        setLoading(false)
      }
    )

    return () => subscription.unsubscribe()
  }, [])

  const signUp = async (data: SignUpData) => {
    try {
      const result = await supabase.auth.signUp({
        email: data.email,
        password: data.password,
        options: {
          data: {
            name: data.name,
            company: data.company,
          }
        }
      })

      //         Owner 
      if (result.data.user && !result.error) {
        try {
          // create_tenant_and_owner     
          console.log(' Creating tenant for user:', result.data.user.id)
          
          const rpcPromise = supabase.rpc('create_tenant_and_owner', {
            tenant_name: data.company || `${data.name} `,
            user_name: data.name,
            user_company: data.company
          })
          
          const rpcTimeoutPromise = new Promise((_, reject) => 
            setTimeout(() => reject(new Error('Tenant creation timeout')), 10000)
          )
          
          const { data: tenantData, error: tenantError } = await Promise.race([
            rpcPromise,
            rpcTimeoutPromise
          ]) as any

          if (tenantError) {
            console.error('Error creating tenant:', tenantError)
            //       
            const { error: profileError } = await supabase
              .from('user_profiles')
              .insert({
                id: result.data.user.id,
                email: data.email,
                name: data.name,
                role: 'owner', //    Owner
                tenant_id: null,
              })

            if (profileError) {
              console.error('Error creating user profile:', profileError)
            }
          } else {
            console.log('    :', tenantData)
          }
        } catch (error) {
          console.error('Error in tenant creation process:', error)
        }
      }

      return result
    } catch (error) {
      console.error('Sign up error:', error)
      return { user: null, error: error as AuthError }
    }
  }

  const signIn = async (data: SignInData) => {
    try {
      const result = await supabase.auth.signInWithPassword({
        email: data.email,
        password: data.password,
      })
      return result
    } catch (error) {
      console.error('Sign in error:', error)
      return { user: null, error: error as AuthError }
    }
  }

  const signOut = async () => {
    try {
      const result = await supabase.auth.signOut()
      setUser(null)
      setSession(null)
      return result
    } catch (error) {
      console.error('Sign out error:', error)
      return { error: error as AuthError }
    }
  }

  const resetPassword = async (email: string) => {
    try {
      const result = await supabase.auth.resetPasswordForEmail(email, {
        redirectTo: `${window.location.origin}/reset-password`,
      })
      return result
    } catch (error) {
      console.error('Reset password error:', error)
      return { error: error as AuthError }
    }
  }

  const value = {
    user,
    session,
    loading,
    signUp,
    signIn,
    signOut,
    resetPassword,
  }

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  )
}