import React, { useState, useMemo, useEffect } from 'react'
import { useSearchParams } from 'react-router-dom'
import { Search, Plus, Grid, AlignLeft, Settings, Trash2, Edit, Eye, Heart } from 'lucide-react'
import { Button, Card, Badge, Input, Select, Modal, Loading } from '@/components/ui'
import { PropertyCreateForm } from '@/components/forms/PropertyCreateForm'
import { useProperties } from '@/hooks/useProperties'
import { useTenant } from '@/contexts/TenantContext'
import type { SimplePropertyFilters, Property, CreatePropertyData } from '@/types'

const PropertiesPageNew: React.FC = () => {
  
  const { tenant } = useTenant()
  const [searchParams, setSearchParams] = useSearchParams()
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedTransactionType, setSelectedTransactionType] = useState('')
  const [selectedPropertyType, setSelectedPropertyType] = useState('')
  const [selectedStatus, setSelectedStatus] = useState<string>('')
  const [viewMode, setViewMode] = useState<'card' | 'list'>('card')
  const [deleteConfirmOpen, setDeleteConfirmOpen] = useState(false)
  const [selectedProperty, setSelectedProperty] = useState<Property | null>(null)
  const [createFormOpen, setCreateFormOpen] = useState(false)
  const [createLoading, setCreateLoading] = useState(false)

  // URL       
  useEffect(() => {
    if (searchParams.get('create') === 'true') {
      setCreateFormOpen(true)
      // URL create  
      const newSearchParams = new URLSearchParams(searchParams)
      newSearchParams.delete('create')
      setSearchParams(newSearchParams, { replace: true })
    }
  }, [searchParams, setSearchParams])

  //   
  const filters = useMemo((): SimplePropertyFilters => {
    const result: SimplePropertyFilters = {}
    
    if (searchTerm) result.search = searchTerm
    if (selectedTransactionType !== '') result.transaction_type = selectedTransactionType
    if (selectedPropertyType !== '') result.property_type = selectedPropertyType
    if (selectedStatus) result.status = selectedStatus as any
    
    return result
  }, [searchTerm, selectedTransactionType, selectedPropertyType, selectedStatus])

  const { 
    properties, 
    loading, 
    error, 
    refreshProperties,
    createProperty,
    updatePropertyStatus,
    deleteProperty 
  } = useProperties(filters)

  const transactionTypeOptions = [
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
  ]

  const propertyTypeOptions = [
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
  ]

  const statusOptions = [
    { value: '', label: ' ' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
  ]

  const formatPrice = (property: Property) => {
    if (property.transaction_type === '' && property.price) {
      return `${(property.price/10000).toFixed(0)} ${(property.price%10000/1000).toFixed(0)}`
    } else if (property.transaction_type === '' && property.deposit) {
      return `${(property.deposit/10000).toFixed(0)} ${(property.deposit%10000/1000).toFixed(0)}`
    } else if (property.transaction_type === '' && property.deposit && property.monthly_rent) {
      return `${property.deposit.toLocaleString()}/${property.monthly_rent}`
    }
    return '  '
  }

  const handleStatusChange = async (propertyId: string, newStatus: Property['status']) => {
    try {
      await updatePropertyStatus(propertyId, newStatus)
    } catch (error) {
      console.error('Status update failed:', error)
    }
  }

  const handleDeleteProperty = async () => {
    if (!selectedProperty) return
    
    try {
      await deleteProperty(selectedProperty.id)
      setDeleteConfirmOpen(false)
      setSelectedProperty(null)
    } catch (error) {
      console.error('Delete failed:', error)
    }
  }

  const resetFilters = () => {
    setSearchTerm('')
    setSelectedTransactionType('')
    setSelectedPropertyType('')
    setSelectedStatus('')
  }

  const handleCreateProperty = async (data: CreatePropertyData) => {
    try {
      setCreateLoading(true)
      await createProperty(data)
      //     
    } catch (error) {
      console.error('  :', error)
      throw error //   
    } finally {
      setCreateLoading(false)
    }
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-96">
        <Loading size="lg" text="  ..." />
      </div>
    )
  }

  if (error) {
    return (
      <div className="text-center py-12">
        <p className="text-red-600 mb-4">{error}</p>
        <Button onClick={refreshProperties}> </Button>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/*   */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900"> </h1>
          <p className="text-gray-600 mt-1">
            {tenant?.name}     <span className="font-semibold text-primary-600">{properties.length}</span> 
            {tenant?.limits.max_properties && (
              <span className="text-gray-500"> / {tenant.limits.max_properties} </span>
            )}
          </p>
        </div>
        <button 
          onClick={() => setCreateFormOpen(true)}
          className="inline-flex items-center px-4 py-2 bg-primary-600 text-white rounded-lg hover:bg-primary-700 transition-colors"
        >
          <Plus size={18} className="mr-2" />
           
        </button>
      </div>

      {/*     */}
      <Card className="p-6">
        <div className="space-y-4">
          {/*  */}
          <div className="relative">
            <Input
              placeholder=",  ..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              leftIcon={<Search size={20} />}
              className="text-base"
            />
          </div>

          {/*     */}
          <div className="flex items-center justify-between flex-wrap gap-4">
            <div className="flex items-center space-x-4">
              {/*   */}
              <Select
                options={transactionTypeOptions}
                value={selectedTransactionType}
                onChange={(e) => setSelectedTransactionType(e.target.value)}
                className="w-32"
              />

              {/*   */}
              <Select
                options={propertyTypeOptions}
                value={selectedPropertyType}
                onChange={(e) => setSelectedPropertyType(e.target.value)}
                className="w-32"
              />

              {/*   */}
              <Select
                options={statusOptions}
                value={selectedStatus}
                onChange={(e) => setSelectedStatus(e.target.value)}
                className="w-32"
              />
            </div>

            <div className="flex items-center space-x-3">
              {/*   */}
              <div className="flex items-center bg-white rounded-lg p-1 border border-gray-300">
                <button
                  onClick={() => setViewMode('card')}
                  className={`flex items-center space-x-2 px-3 py-2 rounded-md transition-colors text-sm font-medium ${
                    viewMode === 'card' 
                      ? 'bg-primary-600 text-white' 
                      : 'text-gray-600 hover:text-gray-800 hover:bg-gray-50'
                  }`}
                >
                  <Grid size={16} />
                  <span></span>
                </button>
                <button
                  onClick={() => setViewMode('list')}
                  className={`flex items-center space-x-2 px-3 py-2 rounded-md transition-colors text-sm font-medium ${
                    viewMode === 'list' 
                      ? 'bg-primary-600 text-white' 
                      : 'text-gray-600 hover:text-gray-800 hover:bg-gray-50'
                  }`}
                >
                  <AlignLeft size={16} />
                  <span></span>
                </button>
              </div>

              {/*   */}
              <Button 
                variant="outline"
                onClick={resetFilters}
                leftIcon={<Settings size={16} />}
              >
                
              </Button>
            </div>
          </div>
        </div>
      </Card>

      {/*   */}
      {properties.length === 0 ? (
        <Card className="p-12 text-center">
          <div className="text-gray-400 mb-4">
            <Search size={48} className="mx-auto" />
          </div>
          <p className="text-gray-500">    .</p>
        </Card>
      ) : viewMode === 'card' ? (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          {properties.map(property => (
            <PropertyCard
              key={property.id}
              property={property}
              onStatusChange={handleStatusChange}
              onDelete={(property) => {
                setSelectedProperty(property)
                setDeleteConfirmOpen(true)
              }}
              formatPrice={formatPrice}
            />
          ))}
        </div>
      ) : (
        <PropertyTable
          properties={properties}
          onStatusChange={handleStatusChange}
          onDelete={(property) => {
            setSelectedProperty(property)
            setDeleteConfirmOpen(true)
          }}
          formatPrice={formatPrice}
        />
      )}

      {/*     */}
      <PropertyCreateForm
        isOpen={createFormOpen}
        onClose={() => setCreateFormOpen(false)}
        onSubmit={handleCreateProperty}
        loading={createLoading}
      />

      {/*    */}
      <Modal
        isOpen={deleteConfirmOpen}
        onClose={() => setDeleteConfirmOpen(false)}
        title=" "
      >
        <div className="space-y-4">
          <p className="text-gray-600">
            '{selectedProperty?.title}'  ?<br />
                .
          </p>
          <div className="flex justify-end space-x-3">
            <Button
              variant="outline"
              onClick={() => setDeleteConfirmOpen(false)}
            >
              
            </Button>
            <Button
              variant="destructive"
              onClick={handleDeleteProperty}
            >
              
            </Button>
          </div>
        </div>
      </Modal>
    </div>
  )
}

//   
interface PropertyCardProps {
  property: Property
  onStatusChange: (propertyId: string, status: Property['status']) => void
  onDelete: (property: Property) => void
  formatPrice: (property: Property) => string
}

const PropertyCard: React.FC<PropertyCardProps> = ({ 
  property, 
  onStatusChange, 
  onDelete, 
  formatPrice 
}) => {
  return (
    <Card className="card-hover overflow-hidden">
      {/*   */}
      <div className="p-4 border-b border-gray-100">
        <div className="flex items-center justify-between mb-3">
          <div className="flex items-center gap-2">
            <Badge variant={
              property.transaction_type === '' ? 'sale' : 
              property.transaction_type === '' ? 'jeonse' : 'monthly'
            }>
              {property.transaction_type}
            </Badge>
            <Badge variant={
              property.status === '' ? 'available' : 
              property.status === '' ? 'reserved' : 'sold'
            }>
              {property.status}
            </Badge>
          </div>
          <div className="flex items-center space-x-1">
            <button className="p-1.5 rounded-full bg-gray-100 text-gray-400 hover:bg-red-500 hover:text-white transition-colors">
              <Heart size={14} fill={property.is_favorite ? 'currentColor' : 'none'} />
            </button>
          </div>
        </div>
        
        <div className="font-bold text-lg text-gray-900">
          {formatPrice(property)}
        </div>
      </div>
      
      {/*   */}
      <div className="p-4">
        <h3 className="font-medium text-sm mb-2 text-ellipsis-2 text-gray-800">
          {property.title}
        </h3>
        <div className="text-xs text-gray-600 mb-3 truncate">
          {property.address}
        </div>

        <div className="text-xs text-gray-600 space-y-1 mb-4">
          <div>
            {property.area}m ({Math.floor(property.area/3.3)}), {property.floor} | {property.type}
          </div>
          {property.landlord_name && (
            <div>: {property.landlord_name} ({property.landlord_phone})</div>
          )}
          {property.exit_date && (
            <div>
              <span>: {property.exit_date}</span>
              <span className="ml-3">: {new Date(property.created_at).toLocaleDateString()}</span>
            </div>
          )}
        </div>

        <div className="grid grid-cols-3 gap-2">
          <select 
            value={property.status}
            onChange={(e) => onStatusChange(property.id, e.target.value as Property['status'])}
            className="text-xs border border-gray-300 rounded px-2 py-1"
          >
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
          <Button size="sm" variant="outline">
            <Edit size={12} className="mr-1" />
            
          </Button>
          <Button 
            size="sm" 
            variant="outline"
            onClick={() => onDelete(property)}
          >
            <Trash2 size={12} className="mr-1" />
            
          </Button>
        </div>
      </div>
    </Card>
  )
}

//   
interface PropertyTableProps {
  properties: Property[]
  onStatusChange: (propertyId: string, status: Property['status']) => void
  onDelete: (property: Property) => void
  formatPrice: (property: Property) => string
}

const PropertyTable: React.FC<PropertyTableProps> = ({ 
  properties, 
  onStatusChange, 
  onDelete, 
  formatPrice 
}) => {
  return (
    <Card>
      {/*   */}
      <div className="bg-gray-50 border-b border-gray-200 px-6 py-3">
        <div className="grid grid-cols-12 gap-4 text-xs font-medium text-gray-700">
          <div className="col-span-1"></div>
          <div className="col-span-4"></div>
          <div className="col-span-2 text-right"></div>
          <div className="col-span-2"></div>
          <div className="col-span-1"></div>
          <div className="col-span-2 text-right"></div>
        </div>
      </div>
      
      {/*   */}
      <div>
        {properties.map(property => (
          <div key={property.id} className="border-b border-gray-200 hover:bg-gray-50 transition-colors">
            <div className="px-6 py-4">
              <div className="grid grid-cols-12 gap-4 items-center text-sm">
                <div className="col-span-1">
                  <Badge size="sm" variant={
                    property.transaction_type === '' ? 'sale' : 
                    property.transaction_type === '' ? 'jeonse' : 'monthly'
                  }>
                    {property.transaction_type}
                  </Badge>
                </div>
                
                <div className="col-span-4">
                  <div className="font-medium text-gray-900 truncate">
                    {property.title}
                  </div>
                  <div className="text-xs text-gray-600 truncate">
                    {property.address} | {property.area}m ({Math.floor(property.area/3.3)}), {property.floor} | {property.type}
                  </div>
                </div>

                <div className="col-span-2 text-right">
                  <div className="font-bold text-gray-900">
                    {formatPrice(property)}
                  </div>
                </div>

                <div className="col-span-2 text-xs text-gray-600">
                  {property.landlord_name ? (
                    <>
                      <div>: {property.landlord_name}</div>
                      <div>{property.landlord_phone}</div>
                    </>
                  ) : (
                    <div className="text-gray-400"> </div>
                  )}
                </div>

                <div className="col-span-1">
                  <select 
                    value={property.status}
                    onChange={(e) => onStatusChange(property.id, e.target.value as Property['status'])}
                    className="text-xs border border-gray-300 rounded px-2 py-1 w-full"
                  >
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                  </select>
                </div>

                <div className="col-span-2 flex items-center justify-end space-x-2">
                  <Button size="sm" variant="outline">
                    <Eye size={12} className="mr-1" />
                    
                  </Button>
                  <Button size="sm" variant="outline">
                    <Edit size={12} className="mr-1" />
                    
                  </Button>
                  <Button 
                    size="sm" 
                    variant="outline"
                    onClick={() => onDelete(property)}
                  >
                    <Trash2 size={12} className="mr-1" />
                    
                  </Button>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
    </Card>
  )
}

export { PropertiesPageNew }